.links_link__3WJjM {
    position: relative;
    text-decoration: none;
    color: #8b7630;
    transition: color .15s ease-in-out;
}

.links_link__3WJjM:hover {
    background-size: 100% 100%;
    cursor: pointer;
}

.links_link__3WJjM:after {
    display: block;
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    width: 100%;
    height: 3px;
    opacity: 0;
    transform: translateY(-150%);
    transition: transform .15s ease-in-out, opacity .15s ease-in-out;
    background-color: gray;
}

